import {
  addFavourite,
  removeFavourite,
} from '../../../api/article';
import { follow, unFollow } from '../../../api/user';

export default {
  name: 'ArticleMeta',
  props:{
    article: {
      type: Object,
      required: true,
    }
  },
  methods: {
    favourite(art){
      if(art.favorited){
        removeFavourite(art.slug)
        art.favorited = false;
        art.favoritesCount += -1;
      }else {
        addFavourite(art.slug)
        art.favorited = true;
        art.favoritesCount += 1;
      }

      // this.$emit('refresh')
    },
    onFollow(art){
      if(art.author.following){
        unFollow(art.author.username)
        art.author.following = false;
      }else {
        follow(art.author.username)
        art.author.following = true;
      }

      // this.$emit('refresh')
    }
  }
};
